import { BrowserRouter, Route } from 'react-router-dom';
import './app.scss';
import { Navigation } from './pages/commonComponents/navigation/Navigation';
import StyledEngineProvider from '@mui/material/StyledEngineProvider';
import { Provider } from 'react-redux';
import { store } from './redux/store';
import { DoOrderAndFAQ } from './pages/commonComponents/doOrderAndFAQ/DoOrderAndFAQ';
import { Footer } from './pages/commonComponents/footer/Footer';
import { MainPage } from './pages/mainPage/MainPage';
import { Switch } from 'react-router';
import { AboutUs } from './pages/aboutUs/AboutUs';
import { FoodPictures } from './pages/commonComponents/foodPictures/FoodPictures';
import { Blog } from './pages/blog/Blog';
import { BusinessLunches } from './pages/businessLunches/BusinessLunches';
import { GastroShop } from './pages/gastroShop/GastroShop';

function App() {
  return (
    <StyledEngineProvider injectFirst>
      <Provider store={store}>
        <BrowserRouter>
          <div className='app'>
            <Navigation />
            <main className='main'>
              <Switch>
                <Route exact path='/gastos' component={MainPage} />
                <Route path='/gastos/foodPrograms' component={MainPage} />
                <Route path='/gastos/aboutUs' component={AboutUs} />
                <Route path='/gastos/blog' component={Blog} />
                <Route path='/gastos/buisnesLanch' component={BusinessLunches} />
                <Route path='/gastos/gastroShop' component={GastroShop} />
              </Switch>
              <Route path={['/gastos/foodPrograms', '/gastos/aboutUs', '/gastos/buisnesLanch', '/gastos/foodPictures']} component={FoodPictures} />
              <Route exact path='/gastos' component={FoodPictures} />
              <Route exact path='/gastos' component={DoOrderAndFAQ} />
              <Route path={['/gastos/foodPrograms', '/gastos/aboutUs', '/gastos/foodPictures']} component={DoOrderAndFAQ} />
            </main>
            <Footer />
          </div>
        </BrowserRouter>
      </Provider>
    </StyledEngineProvider>
  );
}

export default App;
